@font-face {
  font-family: "roboto-example";
  font-weight: normal;
  font-style: normal;
  src: url("/fonts/RobotoRegular/RobotoRegular.eot");
  src: url("/fonts/RobotoRegular/RobotoRegular.eot?#iefix") format("embedded-opentype"), url("/fonts/RobotoRegular/RobotoRegular.woff") format("woff"), url("/fonts/RobotoRegular/RobotoRegular.ttf") format("truetype");
}

@font-face {
  font-family: "roboto-example";
  font-weight: bold;
  font-style: normal;
  src: url("/fonts/RobotoBold/RobotoBold.eot");
  src: url("/fonts/RobotoBold/RobotoBold.eot?#iefix") format("embedded-opentype"), url("/fonts/RobotoBold/RobotoBold.woff") format("woff"), url("/fonts/RobotoBold/RobotoBold.ttf") format("truetype");
}

@keyframes shake {
  0%, to {
    transform: translateZ(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translate3d(-10px, 0, 0);
  }
  30%, 40%, 60%, 80% {
    transform: translate3d(10px, 0, 0);
  }
}

.shake {
  animation-name: shake;
  animation-duration: 1s;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInLeft {
  animation-name: fadeInLeft;
  animation-duration: 0.4s;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInRight {
  animation-name: fadeInRight;
  animation-duration: 0.4s;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInUp {
  animation-name: fadeInUp;
  animation-duration: 0.4s;
  animation-fill-mode: forwards;
  backface-visibility: visible;
}

@keyframes flipInY {
  from {
    transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
    animation-timing-function: ease-in;
    opacity: 0;
  }
  40% {
    transform: perspective(400px) rotate3d(0, 1, 0, -20deg);
    animation-timing-function: ease-in;
  }
  60% {
    transform: perspective(400px) rotate3d(0, 1, 0, 10deg);
  }
  80% {
    transform: perspective(400px) rotate3d(0, 1, 0, -5deg);
  }
  to {
    opacity: 1;
    transform: perspective(400px);
  }
}

.flipInY {
  animation-name: flipInY;
  animation-fill-mode: forwards;
  backface-visibility: visible;
}

@keyframes tada {
  from {
    transform: scale3d(1, 1, 1);
  }
  10%,
  20% {
    transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
  }
  30%,
  50%,
  70%,
  90% {
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
  }
  40%,
  60%,
  80% {
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
  }
  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

::placeholder {
  color: #666;
}

::selection {
  background-color: #972376;
  color: #fff;
}

* {
  box-sizing: border-box;
}

*::after, *::before {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  position: relative;
  line-height: 1.5em;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

img {
  width: 100%;
}

.debug {
  z-index: 999999;
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (max-width: 768px) {
  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(6.66667% - 20px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (max-width: 768px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.productItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: space-around;
}

.productItem {
  padding: 20px;
  margin: 20px 0;
  border-radius: 5px;
  background-color: #fff;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(26.66667% - 20px);
}

@media screen and (max-width: 992px) {
  .productItem {
    width: calc(40% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .productItem {
    width: calc(80% - 20px);
  }
}

.productItem__title {
  margin-top: 15px;
  color: #972376;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}

.productItem__descr {
  margin: 15px 0;
  font-size: 18px;
}

.page404-wrap {
  width: 100vw;
  height: 100vh;
  background-color: #ac2886;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.page404__title {
  margin-bottom: 50px;
  font-weight: 700;
  line-height: normal;
  font-size: 150px;
  text-align: center;
  animation: tada 3s infinite;
}

@media screen and (max-width: 578px) {
  .page404__title {
    font-size: 70px;
    margin-bottom: 30px;
  }
}

.page404__title-small {
  display: block;
  font-size: 50px;
}

@media screen and (max-width: 578px) {
  .page404__title-small {
    font-size: 30px;
  }
}

.page404__subtitle {
  padding: 0 10px;
  text-align: center;
  font-size: 20px;
  margin-bottom: 30px;
}

.page404 .link-wrap {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 578px) {
  .page404 .link-wrap {
    flex-direction: column;
  }
}

.page404 .link-wrap a {
  min-width: 220px;
  margin-right: 0;
  padding: 10px 0;
  justify-content: center;
  color: #fff;
  text-decoration: none;
}

.page404 .link-wrap a span {
  z-index: 1;
}

.page404 .link-wrap a::before {
  z-index: 0;
}

@media screen and (max-width: 578px) {
  .page404 .link-wrap a {
    margin: 20px auto;
    width: 70%;
    min-width: 200px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body *:focus {
  outline: none;
}

body.focus-tab--enable *:focus {
  outline-color: #4d90fe;
  outline-offset: -2px;
  outline-style: auto;
  outline-width: 7px;
}

button:hover, a:hover {
  cursor: pointer;
}

.section-title {
  color: #972376;
  font-family: "Hermes";
  font-size: 48px;
  font-weight: 400;
  line-height: 1.5em;
  text-transform: uppercase;
  animation-name: fadeInUp;
  animation-duration: 0.4s;
}

@media screen and (max-width: 768px) {
  .section-title {
    font-size: 40px;
  }
}

@media screen and (max-width: 576px) {
  .section-title {
    font-size: 30px;
  }
}

.text--uppercase {
  text-transform: uppercase;
}

.color--purple {
  color: #972376;
}

.color--orange {
  color: #f68636;
}

.color--black {
  color: #000;
}

.font-weight--extra {
  font-weight: 800;
}

.color--almost-black {
  color: #343030;
}

.button {
  color: #fff;
  text-align: center;
  border: none;
  border-radius: 28px;
  background-color: #972376;
  background-image: linear-gradient(to top right, #972376 0%, #dc6b47 30%, #972376 70%, #dc6b47 90%, #f68636 95%, #dc6b47 110%);
  background-size: 400% 300%;
  text-transform: uppercase;
  transition: all 0.3s;
  animation-name: gradient-animation;
  animation-duration: 25s;
  animation-iteration-count: infinite;
}

.button:hover {
  transform: scale(0.9);
}

.button-size--md {
  width: 241px;
  height: 56px;
  line-height: 56px;
}

@media screen and (max-width: 578px) {
  .button-size--md {
    height: 50px;
    line-height: 50px;
  }
}

.button-size--xl {
  width: 370px;
  height: 56px;
  line-height: 56px;
}

@media screen and (max-width: 1200px) {
  .button-size--xl {
    width: 300px;
  }
}

@media screen and (max-width: 992px) {
  .button-size--xl {
    width: 224px;
  }
}

.button-size--xxl {
  width: 390px;
  height: 64px;
  line-height: 64px;
}

@media screen and (max-width: 1080px) {
  .button-size--xxl {
    width: 49%;
  }
}

@media screen and (max-width: 576px) {
  .button-size--xxl {
    margin-bottom: 15px;
    width: 80%;
    height: 50px;
    line-height: 50px;
  }
}

@media screen and (max-width: 476px) {
  .button-size--xxl {
    width: 95%;
  }
}

.accent-text {
  font-weight: 800;
  color: #972376;
  text-transform: uppercase;
}
